import React, {useEffect} from 'react';
import {useDispatch} from "react-redux";
import {getAdminInfoAsync, useAdminSelector} from "@/store/slices/admin";

const Home = () => {
    const dispatch:any = useDispatch();
    const {avatar,name} = useAdminSelector();
    useEffect(()=>{
        dispatch(getAdminInfoAsync());
    },[]);
    return (
        <div>
            <p>{name}</p>
            <img src={avatar} alt=""/>
        </div>
    );
};

export default Home;